import React, { useState, useEffect } from 'react';
import Vidstream from './Vidstream';
import './App.css';

export function Episodes({ selectedMovieId }) {
  const [movieId, setMovieId] = useState(selectedMovieId);
  const [results, setResults] = useState("");
  const [selectedEpisodeId, setSelectedEpisodeId] = useState(null);

  useEffect(() => {
    setMovieId(selectedMovieId);
    episodelist();
  }, [selectedEpisodeId]);


  const episodelist = async () => {
    try {
      const apiUrl = `https://api-shiori.vercel.app/movies/flixhq/info?id=${movieId}`;
      const response = await fetch(apiUrl);
      const data = await response.json();
      const fetchedEpisodes = data.episodes;

      setResults(fetchedEpisodes);
      console.log(fetchedEpisodes);
    } catch (error) {
      console.error('Error:', error);
    }
  };
  
  const handleEpisodeClick = (episodeId) => {
    setSelectedEpisodeId(episodeId);
    
  };

  return (
    <div>
      <select
        onChange={(e) => handleEpisodeClick(e.target.value)}
        value={selectedEpisodeId || ''}
      >
        <option value="" disabled>Select an episode</option>
        {results && results.map((list, index) => (
          <option key={index} value={list.id}>
            {index + 1} {list.episode} - {list.title}
          </option>
        ))}
      </select>
      {selectedEpisodeId && <Vidstream selectedEpisodeId={selectedEpisodeId} selectedMovieId={selectedMovieId} />}
    </div>
  );
}
export default Episodes;