import React, { useState, useEffect } from 'react';
import axios from 'axios';
import ReactHLS from 'react-hls';
import './App.css';

function Vidstream({ selectedEpisodeId, selectedMovieId }) {
  const [episodeId, setEpisodeId] = useState(selectedEpisodeId);
  const [mediaId, setMediaId] = useState(selectedMovieId);
  const [video, setVideo] = useState('');
  const [qualityIndex, setQualityIndex] = useState(0); // Default to auto quality

  useEffect(() => {
    setEpisodeId(selectedEpisodeId);
    setMediaId(selectedMovieId);
    geturl();
  }, [selectedEpisodeId, qualityIndex]);

  const geturl = async () => {
    try {
      const response = await axios.get(
        'https://api-shiori.vercel.app/movies/flixhq/watch',
        {
          params: {
            episodeId: episodeId,
            mediaId: mediaId,
          },
        }
      );

      const fetchedUrl = response.data.sources;

      if (fetchedUrl.length > 0) {
        const resultsarray = fetchedUrl[qualityIndex].url;
        console.log(fetchedUrl);
        setVideo(resultsarray);
      } else {
        console.log('Video URL not found in the response.');
      }
    } catch (err) {
      console.log(err);
    }
  };

  const handleQualityChange = (index) => {
    setQualityIndex(index);
    console.log(index.data);
  };

  const qualityOptions = ['1080p', '720p', '360p', 'Auto'];

  return (
    <div className='vidstream'>
      {video && (
        <>
          <div>
            <label htmlFor="qualitySelect">Select Quality: </label>
            <select
              id="qualitySelect"
              value={qualityOptions[qualityIndex]}
              onChange={(e) => handleQualityChange(qualityOptions.indexOf(e.target.value))}
            >
              {qualityOptions.map((option, index) => (
                <option key={index} value={option}>
                  {option}
                </option>
              ))}
            </select>
          </div>
          <ReactHLS url={video} controls autoPlay width='100%' height='100%' />
        </>
      )}
    </div>
  );
}

export default Vidstream;
